@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}
td:not([align]),
th:not([align]) {
  text-align: inherit;
}

html {
  background-color: #FFFFFF;
  font-size: 16px;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  min-width: 300px;
  overflow-x: hidden;
  overflow-y: scroll;
  text-rendering: optimizeLegibility;
  text-size-adjust: 100%;
}

[v-cloak] {
  display: none;
}

article,
aside,
figure,
footer,
header,
hgroup,
section {
  display: block;
}

body,
button,
input,
optgroup,
select,
textarea {
  font-family: "Roboto", "Open Sans", sans-serif;
}

body {
  color: #000000;
  font-size: 1em;
  font-weight: normal;
  line-height: 1.5;
}

a {
  border-bottom: 1px solid #000000;
  color: #000000;
  cursor: pointer;
  letter-spacing: 0.1em;
  padding-bottom: 0.15em;
  text-decoration: none;
  text-transform: uppercase;
  transition: color 0.25s ease-in;
}
a:hover {
  color: #1a1a1a;
  transition: color 0.2s ease-out;
}

main {
  display: flex;
  flex-wrap: wrap;
}

img {
  height: auto;
  max-width: 100%;
}

input[type=checkbox],
input[type=radio] {
  vertical-align: baseline;
}

small {
  font-size: 0.875em;
}

span {
  font-style: inherit;
  font-weight: inherit;
}

fieldset {
  border: none;
}

table,
td,
th {
  vertical-align: top;
}
table:not([align]),
td:not([align]),
th:not([align]) {
  text-align: inherit;
}

@media only screen and (min-width: 768px) and (orientation: landscape) {
  main {
    height: 100vh;
  }
}
.button {
  background-color: #FF5C00;
  border: none;
  border-radius: 25px;
  color: #FFFFFF;
  font-weight: bold;
  letter-spacing: 0.1em;
  margin: 0 auto;
  padding: 1.2em 1em;
  text-transform: uppercase;
}

.card {
  background: #FFFFFF;
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  display: flex;
  flex-direction: column;
  padding: 1em 0.5em;
  text-align: center;
}
.card__image {
  margin: 0 auto;
  width: 280px;
}
.card__body {
  margin-top: 1em;
  font-size: 1.15em;
}
.card__label {
  margin: 0 auto 0.5em;
  text-align: center;
  width: 80%;
}
.card__cost {
  color: #FF5C00;
  font-weight: bold;
  margin-bottom: 1em;
}
.card__button {
  margin-bottom: 2.5em;
  width: 60%;
}

.cart {
  background: #FFFFFF;
  bottom: 0;
  display: flex;
  flex-direction: column;
  left: 100%;
  position: fixed;
  transition: transform 0.25s ease-in;
  top: 0;
  width: 345px;
  z-index: 20;
}
.cart__header {
  align-items: center;
  border-bottom: 1px solid #EBEBEB;
  display: flex;
  font-size: 1.5em;
  justify-content: space-between;
  padding: 1.25rem 2rem;
}
.cart__close {
  width: 14px;
}
.cart__list {
  padding: 1em 1em 1em 2em;
  overflow-y: auto;
}
.cart__item {
  border-bottom: 1px solid #EBEBEB;
  display: flex;
  padding: 0.5em 0;
  margin-top: 0.5em;
}
.cart__image {
  flex-grow: 2;
  margin-right: 1em;
  margin-top: -0.7em;
  margin-left: -0.5em;
  width: 250px;
}
.cart__cost {
  color: #FF5C00;
  font-weight: bold;
  margin-bottom: 0.5em;
}
.cart__remove {
  color: #000000;
  font-size: 0.7em;
  opacity: 0.4;
}
.cart__footer {
  align-items: center;
  border-top: 1px solid #EBEBEB;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: auto;
  padding: 1.25em 2em 2em;
  width: 100%;
}
.cart__subtotal {
  margin-bottom: 1em;
  font-size: 1.15em;
  font-weight: bold;
}
.cart__button {
  margin-bottom: 2em;
  width: 100%;
}
.cart__continue {
  font-size: 0.8em;
  font-weight: bold;
}
.cart--active {
  transform: translateX(-100%);
  transition: transform 0.2s ease-out;
}

@media only screen and (min-width: 768px) and (orientation: landscape) {
  .cart {
    width: 425px;
  }
  .cart__header {
    font-weight: bold;
    padding: 1.25rem 2.5rem;
  }
  .cart__list {
    padding: 0.5em 2em;
  }
  .cart__item {
    padding: 1em 0;
    margin-top: 1em;
  }
  .cart__image {
    margin-right: 1em;
    margin-top: -0.7em;
    width: 275px;
  }
  .cart__cost {
    margin-bottom: 1em;
  }
  .cart__footer {
    padding: 1.25em 3em 2.5em;
  }
}
.image {
  display: block;
  position: relative;
}
.image img {
  display: block;
  height: auto;
  width: 100%;
}
.image.is-fullwidth {
  width: 100%;
}
.image.is-square, .image.is-1by1 {
  padding-top: 100%;
}
.image.is-5by4 {
  padding-top: 80%;
}
.image.is-4by3 {
  padding-top: 75%;
}
.image.is-3by2 {
  padding-top: 66.6666%;
}
.image.is-5by3 {
  padding-top: 60%;
}
.image.is-16by9 {
  padding-top: 56.25%;
}
.image.is-2by1 {
  padding-top: 50%;
}
.image.is-3by1 {
  padding-top: 33.3333%;
}
.image.is-4by5 {
  padding-top: 125%;
}
.image.is-3by4 {
  padding-top: 133.3333%;
}
.image.is-2by3 {
  padding-top: 150%;
}
.image.is-3by5 {
  padding-top: 166.6666%;
}
.image.is-9by16 {
  padding-top: 177.7777%;
}
.image.is-1by2 {
  padding-top: 200%;
}
.image.is-1by3 {
  padding-top: 300%;
}
.image.is-16x16 {
  height: 16px;
  width: 16px;
}
.image.is-20x20 {
  height: 20px;
  width: 20px;
}
.image.is-24x24 {
  height: 24px;
  width: 24px;
}
.image.is-32x32 {
  height: 32px;
  width: 32px;
}
.image.is-48x48 {
  height: 48px;
  width: 48px;
}
.image.is-64x64 {
  height: 64px;
  width: 64px;
}
.image.is-96x96 {
  height: 96px;
  width: 96px;
}
.image.is-128x128 {
  height: 128px;
  width: 128px;
}
.image.is-256x256 {
  height: 256px;
  width: 256px;
}
.image.is-640x640 {
  height: 640px;
  width: 640px;
}

.nav {
  align-items: center;
  background: transparent;
  display: flex;
  justify-content: space-between;
  left: 0;
  padding: 2em 2em 0;
  width: 100%;
}
.nav__image {
  width: 96px;
}
.nav__icon {
  position: relative;
  width: 28px;
}
.nav__icon::after {
  bottom: 4px;
  content: "2";
  font-size: 0.85em;
  font-weight: bold;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
}

@media only screen and (min-width: 768px) and (orientation: landscape) {
  .nav {
    padding: 3em;
    position: fixed;
    top: 0;
  }
}
.overlay {
  background: #000000;
  bottom: 0;
  right: 0;
  left: 0;
  opacity: 0;
  position: fixed;
  top: 0;
  transition: opacity 0.25s ease-in, visibility 0.25s ease-in;
  visibility: hidden;
  z-index: 10;
}
.overlay--active {
  opacity: 0.2;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
  visibility: visible;
}

.section {
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  padding: 4em 2em;
}
.section__left {
  flex: auto;
}
.section__heading {
  font-size: 1.75em;
  margin-bottom: 0.75em;
}
.section__body {
  font-size: 0.95em;
  text-align: center;
  opacity: 0.4;
}
.section--primary {
  background: #EBF6F8;
}

@media only screen and (min-width: 768px) and (orientation: landscape) {
  .section {
    padding: 2em;
  }
  .section__left {
    flex: 1;
  }
  .section__heading {
    font-size: 2.5em;
    margin-bottom: 0.75em;
  }
  .section__body {
    font-size: 1.05em;
    width: 65%;
  }
}
@media only screen and (min-height: 380px) and (orientation: landscape) {
  .section {
    padding-top: 8em;
  }
  .section__heading {
    font-size: 2em;
    margin-bottom: 0.75em;
  }
  .section__body {
    width: 90%;
  }
}
.spinner {
  background: transparent;
  border-radius: 50%;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  color: #FF5C00;
  font-size: 1.25em;
  height: 150px;
  left: 50%;
  letter-spacing: 4px;
  line-height: 150px;
  opacity: 0;
  position: absolute;
  text-align: center;
  text-shadow: 0 0 10px #FF5C00;
  text-transform: uppercase;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: visibility 0.25s ease-in, opacity 0.25s ease-in;
  visibility: hidden;
  width: 150px;
}
.spinner::before {
  animation: spinner 2s linear infinite;
  border: 3px solid transparent;
  border-radius: 50%;
  border-right-color: 3px solid #FF5C00;
  border-top-color: #FF5C00;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.spinner--active {
  opacity: 1;
  transition: visibility 0.2s ease-out, opacity 0.2s ease-out;
  visibility: visible;
}
.spinner__circle {
  animation: animate 2s linear infinite;
  background: transparent;
  display: block;
  height: 4px;
  left: 50%;
  position: absolute;
  top: calc(50% - 2px);
  transform-origin: left;
  width: 50%;
}
.spinner__circle::before {
  background: #FF5C00;
  border-radius: 50%;
  box-shadow: 0 0 20px #FF5C00;
  content: "";
  height: 16px;
  position: absolute;
  right: -6px;
  top: -6px;
  width: 16px;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes animate {
  0% {
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(405deg);
  }
}